@import "../../bower_components/ekko-lightbox/dist/ekko-lightbox.css";
@import "../../bower_components/ionrangeslider/css/ion.rangeSlider.css";
/* Стили для форм */
input[type="button"] {
  outline: none;
}
/* Кастомизация селекта - Месяц (первый вариант) */
.customize-select select {
  font-family: 'Gotham-Medium', Arial, sans-serif !important;
  font-family: 'Gotham-Medium', Arial, sans-serif;
  font-size: 15px;
  color: #5db7d8;
  display: inline-block;
  vertical-align: middle;
  background: transparent;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  cursor: pointer;
  min-width: 60px;
}
.customize-select {
  position: relative;
}
.customize-select:after {
  content: " ";
  width: 10px;
  height: 5px;
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
  background: url(../img/select-arrow.png) no-repeat;
}
.customize-select:before {
  content: " ";
  width: 20px;
  height: 20px;
  background: #eef9fb;
  position: absolute;
  right: 0;
}
/* Стилизация селекта - Месяц (второй вариант) */
.select-list {
  font-family: 'Gotham-Medium', Arial, sans-serif !important;
  font-family: 'Gotham-Medium', Arial, sans-serif;
  font-size: 15px;
  color: #5db7d8;
  position: relative;
  display: inline-block;
  vertical-align: top;
}
.select-list-text {
  cursor: pointer;
  padding-right: 15px;
  background: url(../img/select-arrow.png) no-repeat;
  background-position: 100% 50%;
  display: inline-block;
  vertical-align: top;
}
.select-list .list-drop {
  list-style-type: none;
  cursor: pointer;
  background: #fff;
  padding: 5px 10px 5px 5px;
  border-radius: 4px;
  border: 1px solid #f0f0f0;
  position: absolute;
  top: 20px;
  left: -5px;
  display: none;
  z-index: 999;
  transition: 0.2s ease all;
  box-shadow: 0px 2px 2px 0px rgba(0, 0, 0, 0.1);
}
.select-list .list-drop li {
  margin: 4px 0;
  line-height: 14px;
}
.select-list .list-drop li:hover {
  opacity: 0.8;
}
.select-list .list-drop_pages {
  left: auto;
  right: 0;
}
.select-list.open .list-drop {
  display: block;
}
.select-list label {
  display: inline-block;
  vertical-align: top;
}
.field-comment {
  width: 100%;
  height: 31px;
  display: block;
  font-size: 14px;
  padding: 0 9px;
  border-radius: 2px;
  color: #000000;
  border: 1px solid rgba(0, 0, 0, 0.2);
  outline: none;
}
.field-comment::-webkit-input-placeholder {
  line-height: 20px;
  transition: line-height 0.5s ease;
}
.field-comment::-moz-placeholder {
  line-height: 20px;
  transition: line-height 0.5s ease;
}
.field-comment:-ms-input-placeholder {
  line-height: 20px;
  transition: line-height 0.5s ease;
}
.field-comment::placeholder {
  line-height: 20px;
  transition: line-height 0.5s ease;
}
.field-comment:focus::-webkit-input-placeholder {
  line-height: 350px;
  transition: line-height 0.5s ease;
}
.field-comment:focus::-moz-placeholder {
  line-height: 350px;
  transition: line-height 0.5s ease;
}
.field-comment:focus:-ms-input-placeholder {
  line-height: 350px;
  transition: line-height 0.5s ease;
}
.field-comment:focus::placeholder {
  line-height: 350px;
  transition: line-height 0.5s ease;
}
.buttons-block .btn.btn-edit {
  color: #5db7d8;
  border-color: #5db7d8;
  background: url(../img/icon-pencel.png) no-repeat;
  background-position: 9% 50%;
}
.buttons-block .btn.btn-delete {
  color: #dc4531;
  border-color: #dc4531;
  background: url(../img/icon-trash.png) no-repeat;
  background-position: 12% 50%;
}
/* Общие стили */
.to-left {
  float: left;
}
.to-right {
  float: right;
}
.tar {
  text-align: right;
}
.tal {
  text-align: left;
}
.tac {
  text-align: center;
}
.display {
  display: inherit;
}
/* =================
   main-page styles
   ================= */
/* Стили для боковой панельки */
.sidebar {
  padding: 25px 0 18px;
  border-radius: 10px;
  background: #32bec7;
}
.sidebar_profile {
  text-align: center;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
}
.sidebar .profile-avatar {
  width: 144px;
  height: 144px;
  margin: 0 auto;
  display: inline-block;
  border-radius: 100%;
  border: 3px solid #fff;
}
.sidebar .profile-avatar img {
  width: 100%;
}
.sidebar .profile-name {
  color: #fff;
  width: 150px;
  margin: 0 auto;
  font-size: 20px;
  line-height: 29px;
  padding: 10px 0;
}
.sidebar_menu {
  padding: 18px 10px 0 10px;
}
.sidebar_menu .menu-link {
  display: block;
  margin-bottom: 4px;
}
.sidebar_menu .menu-link:hover a,
.sidebar_menu .menu-link.current a {
  background: rgba(255, 255, 255, 0.3);
}
.sidebar_menu .menu-link a {
  color: #fff;
  display: block;
  padding: 9px 30px 8px 50px;
  line-height: 16.5px;
  border-radius: 30px;
  position: relative;
  transition: 0.5s ease background;
}
.sidebar_menu .icon:before {
  content: "";
  width: 24px;
  height: 24px;
  display: inline-block;
  position: absolute;
  left: 15px;
  top: 50%;
  transform: translateY(-50%);
  background: url(../img/sidebar-icons.png) no-repeat;
  background-position: 0 0;
}
.sidebar_menu .icon-monitor:before {
  background-position: 0 -43px;
}
.sidebar_menu .icon-cart:before {
  background-position: 0 -84px;
}
.sidebar_menu .icon-data:before {
  background-position: 0 -136px;
}
.sidebar_menu .icon-doctor:before {
  background-position: 0 -183px;
}
.sidebar_menu .icon-bonus:before {
  background-position: 0 -224px;
}
/* Стили для желтой плашки (опроса) */
.quiz {
  color: #fff;
  padding: 6px 20px;
  border-radius: 10px;
  position: relative;
  margin-bottom: 16px;
  background: #f5c55e;
}
.quiz .question,
.quiz .answer,
.quiz .why {
  display: inline-block;
  vertical-align: middle;
}
.quiz .question {
  color: #fff;
  margin-right: 12px;
}
.quiz .why {
  font-size: 14px;
  padding-right: 18px;
  color: rgba(255, 255, 255, 0.8);
}
.quiz .text-right {
  text-align: right;
  line-height: 29px;
}
.quiz form {
  font-size: 0;
}
.quiz form label {
  cursor: pointer;
  font-size: 16px;
  margin-right: 2px;
  color: #f5c55e;
  display: inline-block;
  vertical-align: middle;
  padding: 7px 19px 6px;
  border-radius: 20px;
  transition: 0.2s ease all;
  background: rgba(255, 255, 255, 0.8);
}
.quiz form label:hover {
  background: rgba(255, 255, 255, 0.9);
}
.quiz form input[type='radio'] {
  opacity: 0;
  width: 0;
}
.quiz form input[type='radio']:checked + label {
  background: #fff;
}
/* Диаграммы */
.diagrams {
  background: #fff;
  padding: 19px 0 0;
  border-radius: 10px;
  margin-bottom: 16px;
  box-shadow: 0px 1px 1px 0px rgba(0, 0, 0, 0.15);
}
.diagrams .tabs {
  text-align: center;
  margin-bottom: 16px;
}
.diagrams .tabs li {
  margin: 0 3px;
  font-size: 14.5px;
  line-height: 15px;
  padding: 5px 16px;
  border-radius: 20px;
  vertical-align: top;
  display: inline-block;
  color: rgba(0, 0, 0, 0.55);
  cursor: pointer;
}
.diagrams .tabs li.current {
  color: #fff;
  background: #ccc;
}
.diagrams .graph-rating {
  display: none;
}
/* =================
   widgets styles
   ================= */
/* Настроить блоки */
.settings {
  padding: 14px 0;
  text-align: center;
}
.settings a {
  font-family: 'Gotham-Medium', Arial, sans-serif !important;
  font-family: 'Gotham-Medium', Arial, sans-serif;
  display: inline-block;
  vertical-align: middle;
}
.settings .icon-setting:before {
  content: "";
  width: 19px;
  height: 16px;
  margin-right: 8px;
  display: inline-block;
  vertical-align: middle;
  background: url(../img/icon-setting.png) no-repeat;
}
/* Элементы сортировки */
.sorting a {
  font-size: 15px;
  padding: 7px 17px;
  display: inline-block;
  vertical-align: middle;
  border-radius: 20px;
}
.sorting a.current {
  color: #fff;
  background: #5db7d8;
}
.sorting .icon:hover {
  text-decoration: none;
  color: #7ec6e0;
}
.sorting .icon:active {
  text-decoration: none;
  outline: none;
}
.sorting .icon:focus {
  text-decoration: none;
  outline: none;
}
.sorting .icon:before {
  content: "";
  width: 16px;
  height: 20px;
  margin-right: 7px;
  display: inline-block;
  vertical-align: middle;
  background: url(../img/sorting-icons.png) no-repeat;
}
.sorting .icon-recom:before {
  background-position: 0 0;
}
.sorting .icon-recom.current:before {
  background-position: 0 -36px;
}
.sorting .icon-checked:before {
  background-position: -52px 0;
}
.sorting .icon-checked.current:before {
  background-position: -52px -36px;
}
.sorting .icon-cancel:before {
  background-position: -103px 0;
}
.sorting .icon-cancel.current:before {
  background-position: -103px -36px;
}
.sorting .icon-course:before {
  background-position: -154px 0;
}
.sorting .icon-course.current:before {
  background-position: -154px -36px;
}
.sorting .icon-history:before {
  width: 23px;
  background-position: -201px 0;
}
.sorting .icon-history.current:before {
  background-position: -201px -36px;
}
.sorting .icon-directory:before {
  background-position: -257px 0;
}
.sorting .icon-directory.current:before {
  background-position: -257px -36px;
}
/* Пагинация */
.pagination {
  display: block !important;
  padding: 18px 15px 0;
}
.pagination .customize-select {
  font-family: 'Gotham-Medium', Arial, sans-serif !important;
  font-family: 'Gotham-Medium', Arial, sans-serif;
  font-size: 13px;
  color: #5db7d8;
}
.pagination .customize-select select {
  font-size: 13px;
}
.pagination .customize-select:before {
  background: #fff;
}
.pagination .numeration {
  text-align: right;
}
.numeration li {
  margin: 0 2px;
  display: inline-block;
  vertical-align: top;
}
.numeration li a {
  font-family: 'Gotham-Medium', Arial, sans-serif !important;
  font-family: 'Gotham-Medium', Arial, sans-serif;
  font-size: 13px;
}
.numeration li.active a {
  color: #000;
}
/* =================
   верхняя под-плашка
   ================= */
.main-head {
  margin-bottom: 16px;
}
.main-head .lnh {
  line-height: 35px;
}
/* Хлебные крошки */
.breadcrumbs a {
  display: inline-block;
  vertical-align: middle;
  transition: 0.2s ease opacity;
}
.breadcrumbs a:hover {
  opacity: 0.8;
}
.breadcrumbs .back {
  font-family: 'Gotham-Medium', Arial, sans-serif !important;
  font-family: 'Gotham-Medium', Arial, sans-serif;
  font-size: 15px;
  padding-left: 5px;
}
.breadcrumbs .icon-back:before {
  content: "";
  width: 10px;
  height: 17px;
  margin-right: 6px;
  display: inline-block;
  vertical-align: middle;
  background: url(../img/icon-back.png) no-repeat;
}
