}
.security-authorisation__input {
  /* Cначала обозначаем стили для IE8 и более старых версий
	        т.е. здесь мы немного облагораживаем стандартный чекбокс. */
  vertical-align: top;
  margin: 0 3px 0 0;
  width: 25px;
  height: 25px;
  /* Это для всех браузеров, кроме совсем старых, которые не поддерживают
	        селекторы с плюсом. Показываем, что label кликабелен. */
  /* Далее идет оформление чекбокса в современных браузерах, а также IE9 и выше.
	        Благодаря тому, что старые браузеры не поддерживают селекторы :not и :checked,
	        в них все нижеследующие стили не сработают. */
  /* Прячем оригинальный чекбокс. */
  /* Оформление первой части чекбокса в выключенном состоянии (фон). */
  /* Оформление второй части чекбокса в выключенном состоянии (переключатель). */
  /* Меняем фон чекбокса, когда он включен. */
  /* Сдвигаем переключатель чекбокса, когда он включен. */
  /* Меняем фон для включенного состояния */
}
.security-authorisation__input + label {
  cursor: pointer;
}
.security-authorisation__input:not(checked) {
  position: absolute;
  opacity: 0;
}
.security-authorisation__input:not(checked) + label {
  position: relative;
  /* будем позиционировать псевдочекбокс относительно label */
  padding: 7px 14px 0 0;
  /* оставляем справа от label место под псевдочекбокс */
}
.security-authorisation__input:not(checked) + label:before {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  width: 44px;
  height: 27px;
  border-radius: 13px;
  background: rgba(0, 0, 0, 0.2);
}
.security-authorisation__input:not(checked) + label:after {
  content: '';
  position: absolute;
  top: 1px;
  right: 18px;
  width: 24px;
  height: 24px;
  border-radius: 50%;
  background: #ffffff;
  transition: all .2s;
  /* анимация, чтобы чекбокс переключался плавно */
}
.security-authorisation__input:checked + label:after {
  right: 2px;
}
.security-authorisation__input:checked + label:before {
  background: #5db7d8;
}
.security-authorisation__input + label {
  font-family: 'Gotham', Arial, sans-serif !important;
  font-family: 'Gotham', Arial, sans-serif;
  outline: none!important;
  font-size: 14px;
  line-height: 14px;
  margin-right: 15px;
  margin-left: 20px;
}
.security-authorisation__label--unchecked {
  color: #dc4531;
  font: 12.5px/20px sans-serif;
  letter-spacing: 1px;
  outline: none;
  position: relative;
  top: 6px;
}
.security-authorisation__label--checked {
  display: none;
  color: #000000;
  font: 12.5px/20px sans-serif;
  letter-spacing: 1px;
  outline: none;
  position: relative;
  top: 6px;
}
