.notifications-delivery__input:not(checked) + label {
  position: relative;
  /* будем позиционировать псевдочекбокс относительно label */
  padding: 0 70px 0 0;
  /* оставляем справа от label место под псевдочекбокс */
}
.notifications-delivery__input:not(checked) + label:before {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  width: 44px;
  height: 27px;
  border-radius: 13px;
  background: rgba(0, 0, 0, 0.2);
}
.notifications-delivery__input:not(checked) + label:after {
  content: '';
  position: absolute;
  top: 1px;
  right: 18px;
  width: 24px;
  height: 24px;
  border-radius: 50%;
  background: #ffffff;
  transition: all .2s;
  /* анимация, чтобы чекбокс переключался плавно */
}
.notifications-delivery__input:checked + label:after {
