      Благодаря тому, что старые браузеры не поддерживают селекторы :not и :checked,
      в них все нижеследующие стили не сработают. */
  /* Прячем оригинальный чекбокс. */
  /* Оформление первой части чекбокса в выключенном состоянии (фон). */
  /* Оформление второй части чекбокса в выключенном состоянии (переключатель). */
  /* Меняем фон чекбокса, когда он включен. */
  /* Сдвигаем переключатель чекбокса, когда он включен. */
  /* Меняем фон для включенного состояния */
  /* Показываем получение фокуса. */
}
.medcard-docinfo__allow-check + label {
  cursor: pointer;
}
.medcard-docinfo__allow-check:not(checked) {
  position: absolute;
  opacity: 0;
}
.medcard-docinfo__allow-check:not(checked) + label {
  position: relative;
  /* будем позиционировать псевдочекбокс относительно label */
  padding: 0 70px 0 0;
  /* оставляем справа от label место под псевдочекбокс */
}
.medcard-docinfo__allow-check:not(checked) + label:before {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  width: 44px;
  height: 27px;
  border-radius: 13px;
  background: rgba(0, 0, 0, 0.2);
}
.medcard-docinfo__allow-check:not(checked) + label:after {
  content: '';
  position: absolute;
  top: 1px;
  right: 18px;
  width: 24px;
  height: 24px;
  border-radius: 50%;
  background: #ffffff;
  transition: all .2s;
  /* анимация, чтобы чекбокс переключался плавно */
}
.medcard-docinfo__allow-check:checked + label:after {
  right: 2px;
}
.medcard-docinfo__allow-check:checked + label:before {
  background: #5db7d8;
}
.medcard-docinfo__allow-check + label {
  font-family: 'Gotham', Arial, sans-serif !important;
  font-family: 'Gotham', Arial, sans-serif;
  outline: none!important;
  font-size: 14px;
  line-height: 14px;
  margin-right: 15px;
  margin-left: 20px;
}
