.settings__item--disabled .settings__label:before {
  visibility: hidden;
}
.settings__item--disabled .settings__icon {
  opacity: 0.3;
}
.settings__input {
  display: none;
}
.settings__input + .settings__label {
  position: relative;
  display: inline-block;
  cursor: pointer;
  transition: .3s ease all;
  color: #000000;
}
.settings__input + .settings__label:before {
  position: absolute;
  top: 8px;
  right: 8px;
  display: block;
  width: 18px;
  height: 18px;
  content: '';
  background: url(../img/checkbox-sprite-2.png) no-repeat;
}
.settings__input:checked + .settings__label {
  border-color: #5db7d8;
}
.settings__input:checked + .settings__label:before {
  background-position: -57px 0;
}
.settings__label {
  position: relative;
  width: 100%;
  min-height: 148px;
  padding: 5px;
  transition: .3s ease all;
  text-align: center;
  border: 1px solid #ccc;
  border-radius: 15px;
}
.settings__label:hover {
  border-color: #5db7d8;
}
.settings__label:hover:before {
  background-position: -28px 0;
}
.settings__icon {
  display: inline-block;
  width: 31px;
  height: 28px;
  margin-top: 55%;
  vertical-align: middle;
  background: url(../img/items-icon.png) no-repeat;
}
.settings__icon--pulse {
  background-position: 0 0;
}
.settings__icon--pressure {
  background-position: -65px 0;
}
.settings__icon--weight {
  background-position: -137px 0;
}
.settings__icon--temp {
  background-position: 0 -57px;
}
.settings__icon--sugar {
  background-position: -65px -57px;
}
.settings__icon--cholesterol {
  background-position: -137px -57px;
}
.settings__name {
  font-family: 'Gotham-Medium', Arial, sans-serif !important;
  font-family: 'Gotham-Medium', Arial, sans-serif;
  font-size: 12px;
  line-height: 16px;
  position: absolute;
  bottom: 15px;
  left: 50%;
  transform: translateX(-50%);
  letter-spacing: -0.6px;
  color: rgba(0, 0, 0, 0.9);
}
.settings__close {
  position: absolute;
  top: 16px;
  right: 14px;
  display: block;
  width: 11px;
  height: 11px;
  cursor: pointer;
  background: url(../img/icon-exit-11.png) no-repeat;
}
.settings__close:hover {
  opacity: 0.6;
}
